import React, { Component } from 'react';
import { IonContent, IonList, IonPage, IonInput, IonButton, IonItem, IonLabel, IonRow, IonCol, IonToggle, IonImg, IonIcon } from '@ionic/react';
import PropTypes from 'prop-types'
import './Login.page.scss';
import { Plugins,MotionEventResult } from '@capacitor/core';

const { Motion } = Plugins;

const props = {
    userData: PropTypes.any,
    loading: PropTypes.bool,
    loginError: PropTypes.bool,
    handleSubmit: PropTypes.func.isRequired
}

type propTypes = PropTypes.InferProps<typeof props>;

class LoginPage extends Component<propTypes, any> {

    constructor(props: any) {

        super(props)

        this.state = {

            X: 0,

            Y: 0,

            Z: 0,

            u:0,

            acceleration:0

        }

    }

    componentDidUpdate() {

        const {X,Y,Z} = this.state;

        Motion.addListener('accel', async(result: MotionEventResult) => {

            const res2 = result;

 

            console.log({X:result.acceleration.x,Y:result.acceleration.y, Z:result.acceleration.z}, "result");

 

           

      this.setState(() => ({X:result.acceleration.x,Y:result.acceleration.y, Z:result.acceleration.z}))

        //this.setState({acceleration:Math.sqrt(X*X+ Y*Y + Z*Z)})

      

      //v=at --->velocity

           //const  V= this.state.acceleration*result.timeStamp;

        })

 

    }

    render() {

   

 

        return (

            <div>

    <h1>Accelerometer {this.state.X},{this.state.Y},{this.state.Z}</h1>

            </div>

        )

    }
}

export default LoginPage;